<!--  -->
<template>
  <div  class="z-progress-wrapper">
    <div :class="classes" class="z-progress" ref="progress"></div>  
  </div>
</template>

<script>
export default {
  name:"zprogress",
  mounted() {
   
  },
  data () {
    return {
        percent: parseInt(this.percents) 
    };
  },
  props:{
      type:{
        type: String,
        default:"primary",
        validator(type){  
          return ["primary","warning","success","danger","info"].indexOf(type)>-1
        }
      },
      percents:{
        type:String,
        default:"0",
      }
  },
  methods: {
      getPercent(val){
         if(val<0){val=0}
         if(val>100){val=100}
         return val;       
      },
      setWidth(val){
          this.$refs.progress.style.width=val+'%';
          if(val>=100){
              this.$emit('complete')
          }     
      }
  },
  computed: {
      classes(){         
          return `z-progress-${this.type}`
      }
  },
  watch: {
      percent:(newVal,oldVal)=>{
         //this.setWidth(newVal)
         if(newVal>=100){
             oldVal=100;
            
         }
      }
  },
  
  


  
}

</script>
<style lang='scss' scoped>
 .z-progress-wrapper{
     width: 500px;
     height: 10px;
     border:1px solid black;
     border-radius: 10px;
     
 }
 .z-progress{
     height: 100%;
     width: 10%;
     border-radius: 10px;
     background: red;
 }
 .z-progress-primary{
    background: $--button-primary-fill;
 }
 .z-progress-success{
    background: $--button-success-fill;
 }
 .z-progress-info{
    background: $--button-info-fill;
 }
 .z-progress-danger{
    background: $--button-danger-fill;
 }
 .z-progress-warning{
    background: $--button-warning-fill;
 }
</style>


