<!--  -->
<template>
  <div style="padding:0 5%;">
     <h1>Button</h1>
     <div class="import-code"></div>
     <h3>基本用法</h3>
     <zpopover  position="top">
       <div slot="content" >
         <p>1</p>
       </div>
       <zbutton type="danger">top</zbutton>
     </zpopover>
     <zpopover position="bottom">
       <div slot="content">
         <zbutton type="info">12</zbutton>
         <zbutton type="danger">34</zbutton>
       </div>
       <zbutton type="danger">bottom</zbutton>
     </zpopover>
     
     <div class="code">
        <code><<span class='code-entity'>zbutton</span> <span class='code-attr'>type</span>=<span class='code-tag'>info</span>>info</<span class='code-entity'>zbutton</span>></code>
        <code><<span class='code-entity'>zbutton</span> <span class='code-attr'>type</span>=<span class='code-tag'>warning</span>>warning</<span class='code-entity'>zbutton</span>></code>      
         <code> <<span class='code-entity'>zbutton</span> <span class='code-attr'>type</span>=<span class='code-tag'>danger</span>>danger</<span class='code-entity'>zbutton</span>> </code>     
         <code> <<span class='code-entity'>zbutton</span> <span class='code-attr'>type</span>=<span class='code-tag'>success</span>>success</<span class='code-entity'>zbutton</span>> </code>     
         <code> <<span class='code-entity'>zbutton</span> <span class='code-attr'>type</span>=<span class='code-tag'>primary</span>>primary</<span class='code-entity'>zbutton</span>>  </code>                
     </div>
     
     <div class="use-code">Use</div>
     
     <div class="code">
       <code><<span class='code-entity'>zbutton</span> <span class='code-attr'>type</span>=<span class='code-tag'>info-transparent</span>>info</<span class='code-entity'>zbutton</span>></code>  
        <code><<span class='code-entity'>zbutton</span> <span class='code-attr'>type</span>=<span class='code-tag'>warning-transparent</span>>warning</<span class='code-entity'>zbutton</span>></code>      
        <code><<span class='code-entity'>zbutton</span> <span class='code-attr'>type</span>=<span class='code-tag'>danger-transparent</span>>danger</<span class='code-entity'>zbutton</span>> </code>     
        <code><<span class='code-entity'>zbutton</span> <span class='code-attr'>type</span>=<span class='code-tag'>success-transparent</span>>success</<span class='code-entity'>zbutton</span>></code>      
        <code><<span class='code-entity'>zbutton</span> <span class='code-attr'>type</span>=<span class='code-tag'>primary-transparent</span>>primary</<span class='code-entity'>zbutton</span>></code>        
     </div>
     <div>loading</div>
     
     <div class="code">
       <code><<span class='code-entity'>zbutton</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>info</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>loading</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>true</span><span>>loading</</span><span class='code-entity'>zbutton</span>>      
       </code>

       <code><<span class='code-entity'>zbutton</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>warning</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>loading</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>true</span><span>>loading</</span><span class='code-entity'>zbutton</span>>      
       </code>

       <code><<span class='code-entity'>zbutton</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>danger</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>loading</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>true</span><span>>loading</</span><span class='code-entity'>zbutton</span>>      
       </code>

       <code><<span class='code-entity'>zbutton</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>success</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>loading</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>true</span><span>>loading</</span><span class='code-entity'>zbutton</span>>      
       </code>

       <code><<span class='code-entity'>zbutton</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>primary</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>loading</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>true</span><span>>loading</</span><span class='code-entity'>zbutton</span>>      
       </code>
     </div>
     
     <div>touming</div>
     
     <div class="code">
       <code><<span class='code-entity'>zbutton</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>info-transparent</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>loading</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>true</span><span>>loading</</span><span class='code-entity'>zbutton</span>>      
       </code>

       <code><<span class='code-entity'>zbutton</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>warning-transparent</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>loading</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>true</span><span>>loading</</span><span class='code-entity'>zbutton</span>>     
              
       </code>

       <code><<span class='code-entity'>zbutton</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>danger-transparent</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>loading</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>true</span><span>>loading</</span><span class='code-entity'>zbutton</span>>        
              
       </code>

       <code><<span class='code-entity'>zbutton</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>success-transparent</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>loading</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>true</span><span>>loading</</span><span class='code-entity'>zbutton</span>>   
                   
       </code>

       <code><<span class='code-entity'>zbutton</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>warning-transparent</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>loading</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>true</span><span>>loading</</span><span class='code-entity'>zbutton</span>>       
              
       </code>        
     </div>
     <h2>Properties</h2>
     <table>
       <thead>
         <tr>
           <th>Properties</th>
           <th>Description</th>
           <th>Type</th>
           <th>Default</th>
         </tr>
       </thead>
       <tbody>
         
         <tr>
           <td>type</td>
           <td>type name</td>
           <td>String</td>
           <td>primary</td>
         </tr>
         <tr>
           <td>icon-name</td>
           <td>type name</td>
           <td>String</td>
           <td>''</td>
         </tr>
         <tr>
           <td>isLoading</td>
           <td>type name</td>
           <td>Boolean</td>
           <td>false</td>
         </tr>
         <tr>
           <td>type-transparent</td>
           <td>description</td>
           <td>String</td>
           <td>''</td>
         </tr>
       </tbody>
       
     </table>
     
  </div>
</template>

<script>
import zpopover from '../components/zpopover'
import zbutton from '../components/zbutton'
export default {
  methods: {
    
  },
  components:{
    zpopover,
    zbutton
  }
}

</script>
<style scoped lang="scss">
 
</style>


